<template>
  <v-container fluid>
    <v-toolbar flat color="white">
      <v-toolbar-title class="green--text">Devices</v-toolbar-title>
      <v-divider
        class="mx-2"
        inset
        vertical
      ></v-divider>
      <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="search"
        label="Search"
        single-line
        hide-details
      ></v-text-field>

      <v-dialog v-model="dialog" max-width="800px">
        <v-btn slot="activator" color="green lighten-1" dark class="mb-2">New Device</v-btn>
        <v-card>
          <v-card-title>
            <span class="headline">New Device</span>
          </v-card-title>

          <v-card-text>
            <v-container grid-list-md>
              <v-layout wrap>
                <v-flex xs12 sm12 md12 lg12>
                  <v-text-field v-model="editedItem.deviceName" label="Device Name"></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="green lighten-1" flat @click="close">Cancel</v-btn>
            <v-btn color="green lighten-1" flat @click="save">Save</v-btn>

          </v-card-actions>
        </v-card>
      </v-dialog>

    </v-toolbar>
    <v-data-table
      :headers="headers"
      :items="devices"
      :pagination.sync="pagination"
      :search="search"
      class="elevation-1"
    >
      <template slot="items" slot-scope="props">
        <!-- <td class="text-xs-left">{{ props.item.hostId }}</td> -->
        <!-- <td class="text-xs-left">{{ props.item.listingId }}</td> -->
        <td class="text-xs-left">
          <v-icon
            medium
            class="mr-2"
            color="green lighten-1"
            @click="deleteItem(props.item)"
          >
            edit
          </v-icon>
        </td>
        <td class="text-xs-left">{{ props.item.deviceName }}</td>
        <td class="text-xs-left">{{ props.item.lastUpdateOn }}</td>
      </template>
    </v-data-table>
  </v-container>
</template>

<style scoped>
  table.v-table thead tr th {
    font-size: 28px;
  }
  table.v-table tbody tr td {
    font-size: 20px;
  }
</style>

<script>
import { mapState, mapGetters, mapActions } from 'vuex'  
import { CallDialog } from '@/utils'
import store from '@/store'

export default {
  data: () => ({
    search: '',
    dialog: false,
    headers: [
      { text: 'Edit', value: 'deviceId', align: 'left', sortable: false },
      { text: 'Device Name', value: 'deviceName', align: 'left', sortable: false},
      { text: 'Added On', value: 'lastUpdateOn', align: 'left', sortable: true},
    ],
    editedIndex: -1,
    editedItem: {
      listingId: '',
      deviceName: '',
      deviceId: '',
      lastUpdateOn: ''
    },
    defaultItem: {
      listingId: '',
      deviceName: '',
      deviceId: '',
      lastUpdateOn: ''
    },
    pagination: {
      descending: true,
      page: 1,
      rowsPerPage: 25,
      sortBy: 'lastUpdateOn',
      totalItems: 0,
      rowsPerPageItems: [10,25,{"text":"$vuetify.dataIterator.rowsPerPageAll","value":-1}]
    },
  }),

  computed: {
    formTitle () {
      return 'New Device'
    },
    ...mapState({
      devices: state => state.host.devices,
    }),
  },

  watch: {
    dialog (val) {
      val || this.close()
    }
  },
  async beforeRouteEnter (to, from, next) {
    const error = await store.dispatch('host/loadDevices').catch(error => {
      return error
    })

    if (error) {
      CallDialog(error)
    } else {
      next()
    }
  },
  async beforeRouteUpdate (to, from, next) {
    const error = await store.dispatch('host/loadDevices').catch(error => {      
      return error
    })

    if (error) {
      CallDialog(error)
    } else {
      next()
    }
  },
  methods: {

    editItem (item) {
      this.editedIndex = this.devices.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },

    deleteItem (item) {
      // const index = this.desserts.indexOf(item)
      // confirm('Are you sure you want to delete this item?') && this.desserts.splice(index, 1)
    },

    close () {
      this.dialog = false
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      }, 10)
    },
    async save () {
      console.log('this.editedItem', this.editedItem)
      const error = await this.addDevice(this.editedItem).catch(e => {
        console.error('addDevice', e)
        return e
      })

      if (error) {
        CallDialog(error)  
      } else {
        this.close()
      }
    },
    ...mapActions({
      loadDevices: 'host/loadDevices',
      addDevice: 'host/addDevice',
      removeDevice: 'host/removeDevice',
    })
  }
}
</script>